import "../styles/doctorcard.css";
import React, { useState } from "react";
import BookAppointment from "../components/BookAppointment";
import { toast } from "react-hot-toast";

const DoctorCard = ({ ele }) => {
  const [modalOpen, setModalOpen] = useState(false);
  const [token, setToken] = useState(localStorage.getItem("token") || "");

  const handleModal = () => {
    if (token === "") {
      return toast.error("You must log in first");
    }
    setModalOpen(true);
  };

  function formatTimeRange(startHour, endHour) {
    const startAMPM = startHour < 12 ? "AM" : "PM";
    const endAMPM = endHour < 12 ? "AM" : "PM";

    const displayStartHour =
      startHour === 0 ? 12 : startHour > 12 ? startHour - 12 : startHour;
    const displayEndHour =
      endHour === 0 ? 12 : endHour > 12 ? endHour - 12 : endHour;

    const startTime = `${displayStartHour} ${startAMPM}`;
    const endTime = `${displayEndHour} ${endAMPM}`;

    return `${startTime} - ${endTime}`;
  }

  return (
    <div className={`card`}>
      <div className={`card-img flex-center`}>
        <img
          src={
            ele?.userId?.pic ||
            "https://icon-library.com/images/anonymous-avatar-icon/anonymous-avatar-icon-25.jpg"
          }
          alt="profile"
        />
      </div>
      <h3 className="card-name">
        Dr. {ele?.userId?.firstname + " " + ele?.userId?.lastname}
      </h3>
      <p className="specialization">
        <strong>Specialization: </strong>
        {ele?.specialization}
      </p>
      <p className="experience">
        <strong>Experience: </strong>
        {ele?.experience}yrs
      </p>
      <p className="fees">
        <strong>Fees per consultation: </strong>PKR {ele?.fees}
      </p>
      <p className="phone">
        <strong>Phone: </strong>
        {ele?.userId?.mobile}
      </p>
      <p
        className="location"
        style={{
          maxWidth: "300px",
        }}
      >
        <strong>Location: </strong>
        {ele?.location}
      </p>
      <p
        className="location"
        style={{
          maxWidth: "300px",
        }}
      >
        <strong>Availability: </strong>
        {formatTimeRange(ele?.availabilityStartHour, ele?.availabilityEndHour)}
      </p>
      <button className="btn appointment-btn" onClick={handleModal}>
        Book Appointment
      </button>
      {modalOpen && <BookAppointment setModalOpen={setModalOpen} ele={ele} />}
    </div>
  );
};

export default DoctorCard;
