import React, { useEffect, useState } from "react";
import "../styles/bookappointment.css";
import axios from "axios";
import toast from "react-hot-toast";
import { IoMdClose } from "react-icons/io";
import emailJs from "@emailjs/browser";
import { useSelector } from "react-redux";

const BookAppointment = ({ setModalOpen, ele }) => {
  const { userInfo } = useSelector((state) => state.root);
  console.log(ele);
  const [formDetails, setFormDetails] = useState({
    date: "",
    time: "",
  });
  // ele.availabilityEndHour - ele.availabilityStartHour
  const [timeSlots, setTimeSlots] = useState([]);
  const startHour = ele.availabilityStartHour % 12; // Convert to 12-hour format
  const endHour = ele.availabilityEndHour % 12;
  const startAMPM = ele.availabilityStartHour < 12 ? "AM" : "PM";
  const endAMPM = ele.availabilityEndHour < 12 ? "AM" : "PM";

  const getTimeSlots = () => {
    let options = [];
    for (let hour = startHour; hour <= endHour; hour++) {
      const displayHour = hour === 0 ? 12 : hour; // Convert 0 to 12 for 12-hour format
      const displayAMPM = hour < 12 ? startAMPM : endAMPM;
      options.push(
        <option
          key={`${displayHour}-${displayAMPM}`}
          value={`${displayHour}:${displayAMPM}`}
        >
          {`${displayHour}:00 ${displayAMPM}`}
        </option>
      );
    }
    setTimeSlots(options);
  };

  const generateTimeOptions = () => {
    const availabilityStartHour = ele.availabilityStartHour;
    const availabilityEndHour = ele.availabilityEndHour;

    const options = [];
    let hour = availabilityStartHour;
    const endHourNextDay = availabilityEndHour + 24; // Add 24 hours for next day

    while (hour <= endHourNextDay) {
      const displayHour = hour % 24; // Convert to 12-hour format
      const displayAMPM = displayHour < 12 ? "AM" : "PM";
      const displayHour12 =
        displayHour === 0
          ? 12
          : displayHour > 12
          ? displayHour - 12
          : displayHour;
      options.push(
        <option
          key={`${displayHour}-${displayAMPM}`}
          value={`${displayHour12}:00 ${displayAMPM}`}
        >
          {`${displayHour12}:00 ${displayAMPM}`}
        </option>
      );
      hour++;
    }
    return options;
  };

  const inputChange = (e) => {
    const { name, value } = e.target;
    return setFormDetails({
      ...formDetails,
      [name]: value,
    });
  };

  const bookAppointment = async (e) => {
    e.preventDefault();

    // Check if date and time is empty or is in the past
    if (formDetails.date === "" || formDetails.time === "") {
      return toast.error("Please select date and time");
    }

    // Selected Datetime is in the past
    const selectedDate = new Date(`${formDetails.date} ${formDetails.time}`);
    if (selectedDate < new Date()) {
      return toast.error("Please select a future date and time");
    }

    try {
      await toast.promise(
        axios.post(
          "/appointment/bookappointment",
          {
            doctorId: ele?.userId?._id,
            date: formDetails.date,
            time: formDetails.time,
            doctorname: `${ele?.userId?.firstname} ${ele?.userId?.lastname}`,
          },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("token")}`,
            },
          }
        ),
        {
          success: "Appointment booked successfully",
          loading: "Booking appointment...",
          error: (err) =>
            err?.response?.data
              ? err?.response?.data
              : "Unable to book appointment",
        }
      );

      emailJs.init({
        publicKey: process.env.REACT_APP_EMAILJS_PUBLIC_KEY,
        blockHeadless: true,
      });

      await emailJs.send(
        process.env.REACT_APP_EMAILJS_SERVICE_ID ?? "",
        process.env.REACT_APP_EMAILJS_TEMPLATE_ID ?? "",
        {
          from_name: "Doctor In Hand",
          to_name: ele.userId.firstname,
          to_email: ele.userId.email,
          email: ele.userId.email,
          message: `You have an appointment with ${userInfo.firstname} ${userInfo.lastname} on ${formDetails.date} at ${formDetails.time}`,
          reply_to: userInfo.email,
        }
      );

      await emailJs.send(
        process.env.REACT_APP_EMAILJS_SERVICE_ID ?? "",
        process.env.REACT_APP_EMAILJS_TEMPLATE_ID ?? "",
        {
          from_name: "Doctor In Hand",
          to_name: userInfo.firstname,
          to_email: userInfo.email,
          email: userInfo.email,
          message: `You booked an appointment with Dr. ${ele.userId.firstname} for ${formDetails.date} at ${formDetails.time}`,
          reply_to: ele.userId.email,
        }
      );

      setModalOpen(false);
    } catch (error) {
      return error;
    }
  };

  return (
    <>
      <div className="modal flex-center">
        <div className="modal__content">
          <h2 className="page-heading">Book Appointment</h2>
          <IoMdClose
            onClick={() => {
              setModalOpen(false);
            }}
            className="close-btn"
          />
          <div className="register-container flex-center book">
            <form className="register-form">
              <input
                type="date"
                name="date"
                className="form-input"
                value={formDetails.date}
                onChange={inputChange}
              />

              <select
                name="time"
                className="form-input"
                value={formDetails.time}
                onChange={inputChange}
              >
                {generateTimeOptions()}
              </select>

              <button
                type="submit"
                className="btn form-btn"
                onClick={bookAppointment}
              >
                book
              </button>
            </form>
          </div>
        </div>
      </div>
    </>
  );
};

export default BookAppointment;
